import { Component } from '@angular/core';
import { IonicPage, NavController, ModalController, LoadingController, AlertController, NavParams } from 'ionic-angular';
import { GlobalVarsProvider } from '../../providers/global-vars/global-vars';
import { AlertServiceProvider } from '../../providers/alert-service/alert-service';
import { ClientdataProvider } from '../../providers/clientdata/clientdata';
import { ValidatorProvider } from '../../providers/validator/validator'
/**
 * Generated class for the FullstatementPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component( {
  selector: 'page-fullstatement',
  templateUrl: 'fullstatement.html',
} )
export class FullstatementPage {
  accounts: any = [];

  durations: any;
  objectblock: any = {
    username: '',
    account_from: '', startdate: "", enddate: "", lang: '', email: ''
  };
  datetime: string;
  data: string;
  statements: any = [];
  status: string;
  myValue: any;
  title: any;
  start: any;
  langs: any = {};
  end: any;
  error: number = 0;
  difference: any;
  constructor( public validator: ValidatorProvider, private clientdata: ClientdataProvider, public navCtrl: NavController, public navParams: NavParams,
    public modalCtrl: ModalController,
    public globalVars: GlobalVarsProvider, public loadingController: LoadingController, public alertService: AlertServiceProvider, public alertCtrl: AlertController ) {
    this.langs = this.globalVars.doTranslate( this.globalVars.getLang() );
    this.objectblock.username = this.globalVars.Username;
    this.accounts = globalVars.linked_acc;
    this.objectblock.account_from = this.accounts[ 0 ].acc_number;
  }

  ionViewDidLoad() {
    console.log( 'ionViewDidLoad FullstatementPage' );
  }
  goHome() {
    this.navCtrl.setRoot( 'HomePage' );
  }

  processDiff( diff ) {
    let seconds = diff / 1000;
    let minutes = seconds / 60;
    let hours = minutes / 60;
    let days = hours / 24;
    if ( days > 365 ) {
      return true;
    } else {
      return false;
    }
  }

  validate( thevalue ) {
    switch ( thevalue ) {
      case "email": {
        if ( this.validator.isEmail( this.objectblock.email ) )
          this.error = 0;
        else
          this.error = 1;
        break;
      }
    }
  }

  submit() {
    this.start = Date.parse( this.objectblock.startdate );
    this.end = Date.parse( this.objectblock.enddate );
    this.difference = this.end - this.start;
    console.log( this.objectblock );
    if ( this.objectblock.email == undefined || this.objectblock.email == "" ) {
      this.error = 1;
    } else if ( this.objectblock.startdate == undefined || this.objectblock.startdate == "" ) {
      this.error = 2;
    } else if ( this.objectblock.enddate == undefined || this.objectblock.enddate == "" ) {
      this.error = 3;
    }
    else if ( this.difference <= 0 ) {
      this.error = 4;
    } else if ( this.processDiff( this.difference ) ) {
      this.error = 5;
    }
    else {
      this.objectblock.date_from = this.objectblock.enddate;
      this.objectblock.date_to = this.objectblock.startdate;

      this.alertService.showLoading( this.langs.submitting_txt );
      this.clientdata.fullstatement( this.objectblock )
        .subscribe( data => {
          data = this.globalVars.testdec( data );
          console.log( data )
          if ( data.length != 0 ) {
            this.alertService.dismiss();
            if ( !data.error ) {
              this.alertService.errorPop( data.message );
            } else if ( data.error ) {
              this.alertService.errorPop( data.message );
            } else {
              this.alertService.errorPop( this.langs.tech );
            }
          } else {
            this.alertService.myLoadingDismiss();
            this.alertService.errorPop( this.langs.tech );
          }
        }, err => {
          console.log( err );
          this.alertService.dismiss();
          this.alertService.errorPop( this.langs.tech );
        } );
    }

  }
  ionViewDidEnter() {
    this.globalVars.reload();

  }
}



